// pages/sales.js
import { useEffect, useMemo, useState } from "react";
import { useRouter } from "next/router";
import Layout from "../components/Layout";
import Modal from "../components/Modal";
import toast from "react-hot-toast";
import api from "../utils/api";
import { useAuth } from "../context/AuthContext";
import { useInventory } from "../context/InventoryContext";

function formatCurrency(v) {
  return `${Number(v || 0).toLocaleString("ar-SA", {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  })} ÿ±.ÿ≥`;
}

export default function SalesPage() {
  const router = useRouter();
  const { user, hasPermission } = useAuth();
  const { products } = useInventory();

  const [sales, setSales] = useState([]);
  const [loading, setLoading] = useState(true);

  // ŸÅŸÑÿßÿ™ÿ±
  const [search, setSearch] = useState("");
  const [cashierFilter, setCashierFilter] = useState("all");
  const [paymentFilter, setPaymentFilter] = useState("all");
  const [typeFilter, setTypeFilter] = useState("all");

  // ŸÖŸàÿØÿßŸÑÿßÿ™
  const [showAddModal, setShowAddModal] = useState(false);
  const [showDetailsModal, setShowDetailsModal] = useState(false);

  const [selectedSale, setSelectedSale] = useState(null);
  const [selectedSaleItems, setSelectedSaleItems] = useState([]);
  const [detailsLoading, setDetailsLoading] = useState(false);

  // ŸÜŸÖŸàÿ∞ÿ¨ ÿ•ÿ∂ÿßŸÅÿ© ŸÅÿßÿ™Ÿàÿ±ÿ©
  const [saleForm, setSaleForm] = useState({
    customer: "",
    cashier: "",
    paymentMethod: "cash",
    saleType: "sale",
    discount: 0,
    tax: 0,
    items: [],
  });

  // ŸÜŸÖŸàÿ∞ÿ¨ ÿ≥ÿ∑ÿ± ŸÖŸÜÿ™ÿ¨ ŸÅŸä ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
  const [lineProductId, setLineProductId] = useState("");
  const [lineQty, setLineQty] = useState(1);
  const [linePrice, setLinePrice] = useState("");

  // ÿ≠ŸÖÿßŸäÿ© ÿßŸÑÿµŸÑÿßÿ≠Ÿäÿßÿ™
  if (!hasPermission(["admin", "pharmacist", "cashier"])) {
    return (
      <Layout>
        <div
          dir="rtl"
          className="flex items-center justify-center min-h-[60vh] bg-slate-50"
        >
          <div className="px-6 py-4 text-sm font-medium text-red-700 border border-red-200 bg-red-50 rounded-xl">
            ‚ö†Ô∏è ŸÑÿß ŸäŸÖŸÉŸÜŸÉ ÿØÿÆŸàŸÑ Ÿáÿ∞Ÿá ÿßŸÑÿµŸÅÿ≠ÿ©. ÿßŸÑÿ±ÿ¨ÿßÿ° ÿßŸÑÿ™ŸàÿßÿµŸÑ ŸÖÿπ ŸÖÿØŸäÿ± ÿßŸÑŸÜÿ∏ÿßŸÖ ŸÑÿ™ÿ≠ÿØŸäÿ´ ÿµŸÑÿßÿ≠Ÿäÿßÿ™ŸÉ.
          </div>
        </div>
      </Layout>
    );
  }

  // ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™
  useEffect(() => {
    loadSales();
  }, []);

  const loadSales = async () => {
    try {
      setLoading(true);
      const res = await api.get("/sales");
      setSales(res.data || []);
    } catch (err) {
      console.error("loadSales error:", err);
      toast.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™");
    } finally {
      setLoading(false);
    }
  };

  // ÿÆŸäÿßÿ±ÿßÿ™ ÿßŸÑŸÉÿßÿ¥Ÿäÿ± ÿ≠ÿ≥ÿ® ÿßŸÑÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑŸÖŸàÿ¨ŸàÿØÿ©
  const cashierOptions = useMemo(() => {
    const set = new Set();
    (sales || []).forEach((s) => {
      if (s.cashier) set.add(s.cashier);
    });
    return Array.from(set);
  }, [sales]);

  // ÿ•ÿ≠ÿµÿßÿ¶Ÿäÿßÿ™ ÿ≥ÿ±Ÿäÿπÿ©
  const stats = useMemo(() => {
    const today = new Date().toISOString().slice(0, 10); // yyyy-mm-dd

    let totalToday = 0;
    let countToday = 0;
    let totalAll = 0;

    (sales || []).forEach((s) => {
      const dateStr = (s.created_at || "").slice(0, 10);
      const val = Number(s.total || 0);

      totalAll += val;
      if (dateStr === today && s.sale_type === "sale") {
        totalToday += val;
        countToday += 1;
      }
    });

    return {
      totalToday,
      countToday,
      totalAll,
    };
  }, [sales]);

  // ŸÅŸÑÿ™ÿ±ÿ© ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™
  const filteredSales = useMemo(() => {
    const q = search.trim().toLowerCase();
    return (sales || []).filter((s) => {
      const matchSearch =
        !q ||
        s.id?.toString().includes(q) ||
        (s.customer || "").toLowerCase().includes(q);

      const matchCashier =
        cashierFilter === "all" || s.cashier === cashierFilter;

      const matchPayment =
        paymentFilter === "all" || s.payment_method === paymentFilter;

      const matchType =
        typeFilter === "all" || s.sale_type === typeFilter;

      return matchSearch && matchCashier && matchPayment && matchType;
    });
  }, [sales, search, cashierFilter, paymentFilter, typeFilter]);

  // ÿ≠ÿ≥ÿßÿ® ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸäÿßÿ™ ŸÅŸä ÿßŸÑŸÜŸÖŸàÿ∞ÿ¨
  const saleTotals = useMemo(() => {
    const subtotal = (saleForm.items || []).reduce(
      (sum, it) => sum + Number(it.qty || 0) * Number(it.price || 0),
      0
    );
    const discount = Number(saleForm.discount || 0);
    const tax = Number(saleForm.tax || 0);
    const total = subtotal - discount + tax;
    return { subtotal, discount, tax, total };
  }, [saleForm]);

  // ÿ•ÿ∂ÿßŸÅÿ© ÿ≥ÿ∑ÿ± ŸÖŸÜÿ™ÿ¨ ÿ•ŸÑŸâ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
  const handleAddLine = () => {
    if (!lineProductId) {
      toast.error("ÿßÿÆÿ™ÿ± ŸÖŸÜÿ™ÿ¨Ÿãÿß ÿ£ŸàŸÑŸãÿß");
      return;
    }
    const product = products.find((p) => p.id === Number(lineProductId));
    if (!product) {
      toast.error("ÿßŸÑŸÖŸÜÿ™ÿ¨ ÿ∫Ÿäÿ± ŸÖŸàÿ¨ŸàÿØ");
      return;
    }
    const qty = Number(lineQty || 0);
    if (!qty || qty <= 0) {
      toast.error("ÿßŸÑŸÉŸÖŸäÿ© ÿ∫Ÿäÿ± ÿµÿ≠Ÿäÿ≠ÿ©");
      return;
    }

    const price =
      linePrice !== "" ? Number(linePrice) : Number(product.price || 0);

    if (!price || price <= 0) {
      toast.error("ÿ≥ÿπÿ± ÿßŸÑÿ®Ÿäÿπ ÿ∫Ÿäÿ± ÿµÿ≠Ÿäÿ≠");
      return;
    }

    const newItem = {
      productId: product.id,
      productName: product.name,
      qty,
      price,
    };

    setSaleForm((prev) => ({
      ...prev,
      items: [...(prev.items || []), newItem],
    }));

    setLineProductId("");
    setLineQty(1);
    setLinePrice("");
  };

  const handleRemoveLine = (idx) => {
    setSaleForm((prev) => ({
      ...prev,
      items: prev.items.filter((_, i) => i !== idx),
    }));
  };

  // ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
  const handleSaveSale = async () => {
    if (!(saleForm.items || []).length) {
      toast.error("ÿ£ÿ∂ŸÅ ŸÖŸÜÿ™ÿ¨Ÿãÿß Ÿàÿßÿ≠ÿØŸãÿß ÿπŸÑŸâ ÿßŸÑÿ£ŸÇŸÑ ŸÑŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
      return;
    }

    try {
      const payload = {
        customer: saleForm.customer || null,
        cashier: saleForm.cashier || user?.name || null,
        paymentMethod: saleForm.paymentMethod,
        saleType: saleForm.saleType,
        discount: Number(saleForm.discount || 0),
        tax: Number(saleForm.tax || 0),
        items: saleForm.items.map((it) => ({
          productId: it.productId,
          qty: Number(it.qty || 0),
          price: Number(it.price || 0),
        })),
      };

      const res = await api.post("/sales", payload);
      toast.success("ÿ™ŸÖ ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®ŸÜÿ¨ÿßÿ≠");

      // ÿ£ÿ∂ŸÅŸáÿß ŸÑŸÇÿßÿ¶ŸÖÿ© ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™
      setSales((prev) => [res.data, ...prev]);

      // ÿ•ÿπÿßÿØÿ© ÿ™ÿπŸäŸäŸÜ ÿßŸÑŸÜŸÖŸàÿ∞ÿ¨
      setSaleForm({
        customer: "",
        cashier: "",
        paymentMethod: "cash",
        saleType: "sale",
        discount: 0,
        tax: 0,
        items: [],
      });
      setShowAddModal(false);
    } catch (err) {
      console.error("save sale error:", err);
      toast.error("ŸÅÿ¥ŸÑ ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
    }
  };

  // ÿ≠ÿ∞ŸÅ ŸÅÿßÿ™Ÿàÿ±ÿ©
  const handleDeleteSale = async (id) => {
    if (!confirm("ŸáŸÑ ÿ™ÿ±ŸäÿØ ÿ≠ÿ∞ŸÅ Ÿáÿ∞Ÿá ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©ÿü")) return;
    try {
      await api.delete(`/sales/${id}`);
      setSales((prev) => prev.filter((s) => s.id !== id));
      toast.success("ÿ™ŸÖ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
    } catch (err) {
      console.error("delete sale error:", err);
      toast.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ≠ÿ∞ŸÅ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
    }
  };

  // ŸÅÿ™ÿ≠ ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
  const openSaleDetails = async (sale) => {
    setSelectedSale(sale);
    setShowDetailsModal(true);
    setSelectedSaleItems([]);
    setDetailsLoading(true);
    try {
      const res = await api.get(`/sales/${sale.id}`);
      setSelectedSaleItems(res.data.items || []);
    } catch (err) {
      console.error("load sale details error:", err);
      toast.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
    } finally {
      setDetailsLoading(false);
    }
  };

  return (
    <Layout user={user} title="ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™">
      <div dir="rtl" className="space-y-6">
        {/* ÿ±ÿ£ÿ≥ ÿßŸÑÿµŸÅÿ≠ÿ© */}
        <div className="flex flex-col justify-between gap-4 md:flex-row md:items-center">
          <div className="space-y-1">
            <h1 className="flex items-center gap-2 text-2xl font-bold text-slate-800">
              üßæ ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™
            </h1>
            <p className="text-sm text-slate-500">
              ŸÖÿ™ÿßÿ®ÿπÿ© ŸÅŸàÿßÿ™Ÿäÿ± ÿßŸÑÿ®Ÿäÿπ ŸàÿßŸÑŸÖÿ±ÿ™ÿ¨ÿπÿßÿ™ÿå Ÿàÿ≠ÿ±ŸÉÿ© ÿßŸÑŸÉÿßÿ¥Ÿäÿ±ÿå ŸàŸÇŸäŸÖÿ© ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™ ÿßŸÑŸäŸàŸÖŸäÿ©.
            </p>
          </div>

          <div className="flex flex-wrap gap-2">
            <button
              onClick={() => setShowAddModal(true)}
              className="inline-flex items-center gap-2 px-4 py-2 text-sm font-medium text-white rounded-lg shadow-sm bg-emerald-600 hover:bg-emerald-700"
            >
              <span>‚ûï</span>
              <span>ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ¨ÿØŸäÿØÿ©</span>
            </button>

            <button
              onClick={loadSales}
              className="inline-flex items-center gap-2 px-4 py-2 text-sm font-medium border rounded-lg shadow-sm text-slate-700 bg-slate-50 border-slate-200 hover:bg-slate-100"
            >
              üîÑ ÿ™ÿ≠ÿØŸäÿ´
            </button>
          </div>
        </div>

        {/* ŸÉÿ±Ÿàÿ™ ÿ•ÿ≠ÿµÿßÿ¶Ÿäÿßÿ™ */}
        <div className="grid grid-cols-1 gap-4 md:grid-cols-3">
          <StatCard
            label="ÿ•ÿ¨ŸÖÿßŸÑŸä ŸÖÿ®Ÿäÿπÿßÿ™ ÿßŸÑŸäŸàŸÖ"
            value={formatCurrency(stats.totalToday)}
            icon="üìÖ"
            color="bg-emerald-50 text-emerald-700 border-emerald-100"
          />
          <StatCard
            label="ÿπÿØÿØ ŸÅŸàÿßÿ™Ÿäÿ± ÿßŸÑŸäŸàŸÖ"
            value={stats.countToday.toLocaleString("ar-SA")}
            icon="üßÆ"
            color="bg-sky-50 text-sky-700 border-sky-100"
          />
          <StatCard
            label="ÿ•ÿ¨ŸÖÿßŸÑŸä ŸÖÿ®Ÿäÿπÿßÿ™ ÿßŸÑŸÜÿ∏ÿßŸÖ"
            value={formatCurrency(stats.totalAll)}
            icon="üí∞"
            color="bg-amber-50 text-amber-700 border-amber-100"
          />
        </div>

        {/* ÿßŸÑŸÅŸÑÿßÿ™ÿ± ŸàÿßŸÑÿ®ÿ≠ÿ´ */}
        <div className="p-4 space-y-4 bg-white border shadow-sm rounded-2xl">
          <div className="relative">
            <span className="absolute text-slate-400 left-3 top-2.5">üîé</span>
            <input
              type="text"
              placeholder="ÿ®ÿ≠ÿ´ ÿ®ÿ±ŸÇŸÖ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© ÿ£Ÿà ÿßÿ≥ŸÖ ÿßŸÑÿπŸÖŸäŸÑ‚Ä¶"
              className="w-full p-3 pr-3 text-sm border rounded-xl pl-9 border-slate-200 focus:outline-none focus:ring-2 focus:ring-emerald-500/60 focus:border-emerald-500"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
          </div>

          <div className="flex flex-wrap items-center gap-3 text-sm">
            {/* ŸÉÿßÿ¥Ÿäÿ± */}
            <div className="flex flex-wrap items-center gap-2">
              <span className="text-xs text-slate-500">ÿßŸÑŸÉÿßÿ¥Ÿäÿ±:</span>
              <select
                className="p-2 text-xs border rounded-lg border-slate-200 bg-slate-50"
                value={cashierFilter}
                onChange={(e) => setCashierFilter(e.target.value)}
              >
                <option value="all">ŸÉŸÑ ÿßŸÑŸÉÿßÿ¥Ÿäÿ±</option>
                {cashierOptions.map((c) => (
                  <option key={c} value={c}>
                    {c}
                  </option>
                ))}
              </select>
            </div>

            {/* ÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ */}
            <div className="flex flex-wrap items-center gap-2">
              <span className="text-xs text-slate-500">ÿßŸÑÿØŸÅÿπ:</span>
              <select
                className="p-2 text-xs border rounded-lg border-slate-200 bg-slate-50"
                value={paymentFilter}
                onChange={(e) => setPaymentFilter(e.target.value)}
              >
                <option value="all">ÿßŸÑŸÉŸÑ</option>
                <option value="cash">ŸÜŸÇÿØŸãÿß</option>
                <option value="card">ÿ®ÿ∑ÿßŸÇÿ©</option>
                <option value="wallet">ŸÖÿ≠ŸÅÿ∏ÿ©</option>
              </select>
            </div>

            {/* ŸÜŸàÿπ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© */}
            <div className="flex flex-wrap items-center gap-2">
              <span className="text-xs text-slate-500">ÿßŸÑŸÜŸàÿπ:</span>
              <select
                className="p-2 text-xs border rounded-lg border-slate-200 bg-slate-50"
                value={typeFilter}
                onChange={(e) => setTypeFilter(e.target.value)}
              >
                <option value="all">ÿßŸÑŸÉŸÑ</option>
                <option value="sale">ÿ®Ÿäÿπ</option>
                <option value="return">ŸÖÿ±ÿ™ÿ¨ÿπ</option>
              </select>
            </div>
          </div>
        </div>

        {/* ÿ¨ÿØŸàŸÑ ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™ */}
        <div className="overflow-x-auto bg-white border shadow-sm rounded-2xl">
          {loading ? (
            <div className="p-6 text-sm text-center text-slate-500">
              üîÑ ÿ¨ÿßÿ±Ÿä ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™‚Ä¶
            </div>
          ) : (
            <table className="w-full text-sm text-right min-w-[900px]">
              <thead className="text-xs uppercase border-b bg-slate-50 text-slate-500">
                <tr>
                  <th className="p-3 font-medium">#</th>
                  <th className="p-3 font-medium">ÿßŸÑÿ™ÿßÿ±ŸäÿÆ</th>
                  <th className="p-3 font-medium">ÿßŸÑÿπŸÖŸäŸÑ</th>
                  <th className="p-3 font-medium">ÿßŸÑŸÉÿßÿ¥Ÿäÿ±</th>
                  <th className="p-3 font-medium">ÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ</th>
                  <th className="p-3 font-medium">ÿßŸÑŸÜŸàÿπ</th>
                  <th className="p-3 font-medium">ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
                  <th className="p-3 font-medium text-center">ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
                </tr>
              </thead>
              <tbody>
                {filteredSales.map((s) => (
                  <tr
                    key={s.id}
                    className="transition-colors border-t border-slate-100 even:bg-slate-50/40 hover:bg-slate-100/60"
                  >
                    <td className="p-3 text-slate-700">{s.id}</td>
                    <td className="p-3 text-slate-700">
                      {s.created_at
                        ? new Date(s.created_at).toLocaleString("ar-EG")
                        : "-"}
                    </td>
                    <td className="p-3 text-slate-700">
                      {s.customer || "-"}
                    </td>
                    <td className="p-3 text-slate-700">
                      {s.cashier || "-"}
                    </td>
                    <td className="p-3 text-slate-700">
                      {s.payment_method === "cash"
                        ? "ŸÜŸÇÿØŸãÿß"
                        : s.payment_method === "card"
                        ? "ÿ®ÿ∑ÿßŸÇÿ©"
                        : s.payment_method === "wallet"
                        ? "ŸÖÿ≠ŸÅÿ∏ÿ©"
                        : s.payment_method || "-"}
                    </td>
                    <td className="p-3 text-slate-700">
                      <span
                        className={`px-2 py-0.5 text-xs rounded-full ${
                          s.sale_type === "sale"
                            ? "bg-emerald-50 text-emerald-700"
                            : "bg-amber-50 text-amber-700"
                        }`}
                      >
                        {s.sale_type === "sale" ? "ÿ®Ÿäÿπ" : "ŸÖÿ±ÿ™ÿ¨ÿπ"}
                      </span>
                    </td>
                    <td className="p-3 font-semibold text-slate-900">
                      {formatCurrency(s.total)}
                    </td>
                    <td className="p-3 text-center">
                      <div className="flex flex-wrap justify-center gap-1">
                        <button
                          onClick={() => openSaleDetails(s)}
                          className="px-3 py-1 text-xs font-medium text-indigo-700 rounded-lg bg-indigo-50 hover:bg-indigo-100"
                        >
                          üîç ÿ™ŸÅÿßÿµŸäŸÑ
                        </button>
                        <button
                          onClick={() => handleDeleteSale(s.id)}
                          className="px-3 py-1 text-xs font-medium text-red-700 rounded-lg bg-red-50 hover:bg-red-100"
                        >
                          üóëÔ∏è ÿ≠ÿ∞ŸÅ
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}

                {!filteredSales.length && !loading && (
                  <tr>
                    <td
                      colSpan={8}
                      className="p-6 text-sm text-center text-slate-400"
                    >
                      ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÅŸàÿßÿ™Ÿäÿ± ŸÖÿ∑ÿßÿ®ŸÇÿ© ŸÑŸÑÿ®ÿ≠ÿ´ / ÿßŸÑŸÅŸÑÿßÿ™ÿ± ÿßŸÑÿ≠ÿßŸÑŸäÿ©‚Ä¶
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          )}
        </div>

        {/* ŸÖŸàÿØÿßŸÑ ÿ•ÿ∂ÿßŸÅÿ© ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ¨ÿØŸäÿØÿ© */}
        {showAddModal && (
          <Modal
            title="ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ¨ÿØŸäÿØÿ©"
            onClose={() => setShowAddModal(false)}
            onConfirm={handleSaveSale}
            confirmLabel="ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©"
          >
            <div className="space-y-4 text-sm" dir="rtl">
              {/* ÿ®ŸäÿßŸÜÿßÿ™ ÿπÿßŸÖÿ© */}
              <div className="grid grid-cols-1 gap-3 md:grid-cols-2">
                <Field label="ÿßŸÑÿπŸÖŸäŸÑ">
                  <input
                    className="w-full p-2 border rounded-lg border-slate-200"
                    value={saleForm.customer}
                    onChange={(e) =>
                      setSaleForm((prev) => ({
                        ...prev,
                        customer: e.target.value,
                      }))
                    }
                  />
                </Field>
                <Field label="ÿßŸÑŸÉÿßÿ¥Ÿäÿ±">
                  <input
                    className="w-full p-2 border rounded-lg border-slate-200"
                    value={saleForm.cashier}
                    onChange={(e) =>
                      setSaleForm((prev) => ({
                        ...prev,
                        cashier: e.target.value,
                      }))
                    }
                    placeholder={user?.name || ""}
                  />
                </Field>
                <Field label="ÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ">
                  <select
                    className="w-full p-2 border rounded-lg border-slate-200"
                    value={saleForm.paymentMethod}
                    onChange={(e) =>
                      setSaleForm((prev) => ({
                        ...prev,
                        paymentMethod: e.target.value,
                      }))
                    }
                  >
                    <option value="cash">ŸÜŸÇÿØŸãÿß</option>
                    <option value="card">ÿ®ÿ∑ÿßŸÇÿ©</option>
                    <option value="wallet">ŸÖÿ≠ŸÅÿ∏ÿ©</option>
                  </select>
                </Field>
                <Field label="ŸÜŸàÿπ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©">
                  <select
                    className="w-full p-2 border rounded-lg border-slate-200"
                    value={saleForm.saleType}
                    onChange={(e) =>
                      setSaleForm((prev) => ({
                        ...prev,
                        saleType: e.target.value,
                      }))
                    }
                  >
                    <option value="sale">ÿ®Ÿäÿπ</option>
                    <option value="return">ŸÖÿ±ÿ™ÿ¨ÿπ</option>
                  </select>
                </Field>
              </div>

              {/* ÿ≥ÿ∑ÿ± ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ */}
              <div className="p-3 space-y-2 border rounded-xl border-slate-200 bg-slate-50/60">
                <p className="text-xs font-semibold text-slate-600">
                  ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ ŸÑŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
                </p>
                <div className="grid items-end grid-cols-1 gap-2 md:grid-cols-4">
                  <div className="md:col-span-2">
                    <label className="block mb-1 text-xs text-slate-500">
                      ÿßŸÑŸÖŸÜÿ™ÿ¨
                    </label>
                    <select
                      className="w-full p-2 text-sm border rounded-lg border-slate-200"
                      value={lineProductId}
                      onChange={(e) => {
                        const v = e.target.value;
                        setLineProductId(v);
                        const prod = products.find(
                          (p) => p.id === Number(v)
                        );
                        if (prod) setLinePrice(prod.price || "");
                      }}
                    >
                      <option value="">ÿßÿÆÿ™ÿ± ŸÖŸÜÿ™ÿ¨Ÿãÿß‚Ä¶</option>
                      {products.map((p) => (
                        <option key={p.id} value={p.id}>
                          {p.name} ({p.sku})
                        </option>
                      ))}
                    </select>
                  </div>
                  <div>
                    <label className="block mb-1 text-xs text-slate-500">
                      ÿßŸÑŸÉŸÖŸäÿ©
                    </label>
                    <input
                      type="number"
                      min={1}
                      className="w-full p-2 border rounded-lg border-slate-200"
                      value={lineQty}
                      onChange={(e) => setLineQty(e.target.value)}
                    />
                  </div>
                  <div>
                    <label className="block mb-1 text-xs text-slate-500">
                      ÿ≥ÿπÿ± ÿßŸÑŸàÿ≠ÿØÿ©
                    </label>
                    <input
                      type="number"
                      className="w-full p-2 border rounded-lg border-slate-200"
                      value={linePrice}
                      onChange={(e) => setLinePrice(e.target.value)}
                    />
                  </div>
                </div>
                <div className="flex justify-end">
                  <button
                    type="button"
                    onClick={handleAddLine}
                    className="px-3 py-1 mt-1 text-xs font-medium text-white rounded-lg bg-emerald-600 hover:bg-emerald-700"
                  >
                    ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÑŸÑÿ≥ŸÑÿ©
                  </button>
                </div>
              </div>

              {/* ÿ¨ÿØŸàŸÑ ÿßŸÑÿπŸÜÿßÿµÿ± ÿØÿßÿÆŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© */}
              <div className="mt-3">
                <p className="mb-2 text-xs font-semibold text-slate-600">
                  ÿßŸÑÿπŸÜÿßÿµÿ± ÿßŸÑŸÖÿ∂ÿßŸÅÿ©:
                </p>
                {(saleForm.items || []).length ? (
                  <div className="overflow-x-auto border rounded-lg border-slate-200">
                    <table className="w-full text-xs text-right">
                      <thead className="bg-slate-50">
                        <tr>
                          <th className="p-2">ÿßŸÑŸÖŸÜÿ™ÿ¨</th>
                          <th className="p-2">ÿßŸÑŸÉŸÖŸäÿ©</th>
                          <th className="p-2">ÿ≥ÿπÿ± ÿßŸÑŸàÿ≠ÿØÿ©</th>
                          <th className="p-2">ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
                          <th className="p-2 text-center">ÿ≠ÿ∞ŸÅ</th>
                        </tr>
                      </thead>
                      <tbody>
                        {saleForm.items.map((it, i) => (
                          <tr key={i} className="border-t">
                            <td className="p-2">{it.productName}</td>
                            <td className="p-2">{it.qty}</td>
                            <td className="p-2">
                              {formatCurrency(it.price)}
                            </td>
                            <td className="p-2">
                              {formatCurrency(
                                Number(it.qty || 0) *
                                  Number(it.price || 0)
                              )}
                            </td>
                            <td className="p-2 text-center">
                              <button
                                type="button"
                                onClick={() => handleRemoveLine(i)}
                                className="px-2 py-1 text-xs text-red-700 rounded bg-red-50 hover:bg-red-100"
                              >
                                üóëÔ∏è
                              </button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                ) : (
                  <p className="text-xs text-slate-400">
                    ŸÑŸÖ ÿ™ÿ™ŸÖ ÿ•ÿ∂ÿßŸÅÿ© ÿ£Ÿä ŸÖŸÜÿ™ÿ¨ ÿ®ÿπÿØ.
                  </p>
                )}
              </div>

              {/* ÿßŸÑÿÆÿµŸÖ ŸàÿßŸÑÿ∂ÿ±Ÿäÿ®ÿ© ŸàÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸäÿßÿ™ */}
              <div className="grid grid-cols-1 gap-3 mt-4 md:grid-cols-3">
                <Field label="ÿßŸÑÿÆÿµŸÖ">
                  <input
                    type="number"
                    className="w-full p-2 border rounded-lg border-slate-200"
                    value={saleForm.discount}
                    onChange={(e) =>
                      setSaleForm((prev) => ({
                        ...prev,
                        discount: e.target.value,
                      }))
                    }
                  />
                </Field>
                <Field label="ÿßŸÑÿ∂ÿ±Ÿäÿ®ÿ©">
                  <input
                    type="number"
                    className="w-full p-2 border rounded-lg border-slate-200"
                    value={saleForm.tax}
                    onChange={(e) =>
                      setSaleForm((prev) => ({
                        ...prev,
                        tax: e.target.value,
                      }))
                    }
                  />
                </Field>
                <div className="p-2 text-xs border rounded-lg border-slate-200 bg-slate-50">
                  <p className="flex items-center justify-between">
                    <span className="text-slate-500">ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ŸÇÿ®ŸÑ:</span>
                    <span className="font-semibold">
                      {formatCurrency(saleTotals.subtotal)}
                    </span>
                  </p>
                  <p className="flex items-center justify-between">
                    <span className="text-slate-500">ÿßŸÑÿÆÿµŸÖ:</span>
                    <span>{formatCurrency(saleTotals.discount)}</span>
                  </p>
                  <p className="flex items-center justify-between">
                    <span className="text-slate-500">ÿßŸÑÿ∂ÿ±Ÿäÿ®ÿ©:</span>
                    <span>{formatCurrency(saleTotals.tax)}</span>
                  </p>
                  <p className="flex items-center justify-between mt-1 text-emerald-700">
                    <span className="font-semibold">ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÜŸáÿßÿ¶Ÿä:</span>
                    <span className="font-bold">
                      {formatCurrency(saleTotals.total)}
                    </span>
                  </p>
                </div>
              </div>
            </div>
          </Modal>
        )}

        {/* ŸÖŸàÿØÿßŸÑ ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© */}
        {showDetailsModal && selectedSale && (
          <Modal
            title={`ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© ÿ±ŸÇŸÖ #${selectedSale.id}`}
            onClose={() => setShowDetailsModal(false)}
            onConfirm={() => setShowDetailsModal(false)}
            confirmLabel="ÿ•ÿ∫ŸÑÿßŸÇ"
          >
            <div className="space-y-3 text-sm" dir="rtl">
              <p>
                <strong>ÿßŸÑÿπŸÖŸäŸÑ:</strong> {selectedSale.customer || "-"}
              </p>
              <p>
                <strong>ÿßŸÑŸÉÿßÿ¥Ÿäÿ±:</strong> {selectedSale.cashier || "-"}
              </p>
              <p>
                <strong>ŸÜŸàÿπ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©:</strong>{" "}
                {selectedSale.sale_type === "sale" ? "ÿ®Ÿäÿπ" : "ŸÖÿ±ÿ™ÿ¨ÿπ"}
              </p>
              <p>
                <strong>ÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ:</strong>{" "}
                {selectedSale.payment_method === "cash"
                  ? "ŸÜŸÇÿØŸãÿß"
                  : selectedSale.payment_method === "card"
                  ? "ÿ®ÿ∑ÿßŸÇÿ©"
                  : selectedSale.payment_method === "wallet"
                  ? "ŸÖÿ≠ŸÅÿ∏ÿ©"
                  : selectedSale.payment_method || "-"}
              </p>
              <p>
                <strong>ÿßŸÑÿ™ÿßÿ±ŸäÿÆ:</strong>{" "}
                {selectedSale.created_at
                  ? new Date(selectedSale.created_at).toLocaleString(
                      "ar-EG"
                    )
                  : "-"}
              </p>

              <hr className="my-2" />

              <p className="text-xs font-semibold text-slate-600">
                ÿßŸÑÿπŸÜÿßÿµÿ±:
              </p>

              {detailsLoading ? (
                <p className="text-xs text-slate-500">
                  üîÑ ÿ¨ÿßÿ±Ÿä ÿ™ÿ≠ŸÖŸäŸÑ ÿ™ŸÅÿßÿµŸäŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©‚Ä¶
                </p>
              ) : (selectedSaleItems || []).length ? (
                <div className="overflow-x-auto border rounded-lg border-slate-200">
                  <table className="w-full text-xs text-right">
                    <thead className="bg-slate-50">
                      <tr>
                        <th className="p-2">ÿßŸÑŸÖŸÜÿ™ÿ¨</th>
                        <th className="p-2">ÿßŸÑŸÉŸÖŸäÿ©</th>
                        <th className="p-2">ÿ≥ÿπÿ± ÿßŸÑŸàÿ≠ÿØÿ©</th>
                        <th className="p-2">ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
                      </tr>
                    </thead>
                    <tbody>
                      {selectedSaleItems.map((it) => (
                        <tr key={it.id} className="border-t">
                          <td className="p-2">
                            {it.product_name || it.productId}
                          </td>
                          <td className="p-2">{it.qty}</td>
                          <td className="p-2">
                            {formatCurrency(it.unit_price)}
                          </td>
                          <td className="p-2">
                            {formatCurrency(it.total_price)}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              ) : (
                <p className="text-xs text-slate-400">
                  ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿπŸÜÿßÿµÿ± ŸÖÿ≠ŸÅŸàÿ∏ÿ© ŸÑŸáÿ∞Ÿá ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©.
                </p>
              )}

              <hr className="my-2" />
              <p className="flex items-center justify-between text-xs">
                <span>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÜŸáÿßÿ¶Ÿä:</span>
                <span className="font-bold text-emerald-700">
                  {formatCurrency(selectedSale.total)}
                </span>
              </p>
            </div>
          </Modal>
        )}
      </div>
    </Layout>
  );
}

// ÿ®ÿ∑ÿßŸÇÿ© ÿ•ÿ≠ÿµÿßÿ¶Ÿäÿ©
function StatCard({ label, value, icon, color }) {
  return (
    <div
      className={`flex items-center justify-between p-4 border rounded-2xl ${color}`}
    >
      <div className="space-y-1">
        <p className="text-xs font-medium text-slate-500">{label}</p>
        <p className="text-lg font-bold">{value}</p>
      </div>
      <div className="flex items-center justify-center w-10 h-10 text-lg rounded-full bg-white/70">
        {icon}
      </div>
    </div>
  );
}

function Field({ label, children }) {
  return (
    <div>
      <label className="block mb-1 text-xs text-slate-600">{label}</label>
      {children}
    </div>
  );
}




















